.navbar {
  display: flex;
  width: 100%;
  justify-content: space-around;
  margin: 20px auto;
  align-items: center;
}

.navbar_left {
  display: flex;
  justify-content: space-around;
  width: 30%;
  margin-left: 20px;
}

.navbar_left img {
  margin-right: 20px;
}

.links {
  display: flex;
  flex-direction: row;
  gap: 10px;
}

.links h4 {
  color: hsl(0, 0%, 75%);
  font-weight: 700;
}

.links h4:hover {
  cursor: pointer;
  color: hsl(257, 27%, 26%);
}

.navbar_right {
  display: flex;
  width: 20%;
  gap: 30px;
}

.btn {
  border: none;
  background-color: white;
  font-size: 18px;
  color: hsl(0, 0%, 75%);
}

.btn:hover {
  background-color: hsl(180, 66%, 49%);
  cursor: pointer;
  width: 80px;
  height: 25px;
  border: none;
  border-radius: 20px;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 375px) {
  .navbar {
    width: 100%;
    display: flex;
    margin-top: 10px;
    padding: 10px;
  }

  .navbar_left {
    width: 50%;
    margin-left: 0px;
  }

  .navbar_left img {
    width: 40px;
    height: 20px;
    margin-right: 10px;

  }

  .links h4 {
    font-size: 12px;
  }

  .navbar_right {
    width: 30%;
    gap: 5px;
    display: flex;
    justify-content:flex-end;
  }

  .btn {
    font-size: 14px;
    font-weight: bold;

  }
}